.home{
    width: 100%;
    overflow-y: scroll;
    overflow: hidden;
    
}

.hero{
    height:calc(70vh - 60px);
    width: 100%;
    background-color: var(---mainColor);
    color: #fff;
    border-bottom-left-radius: 300px;
    position: relative;
    display: flex;
    justify-content: center;

  }
.rightSide{
     width: 50%;
     transform:translate(50%,0%);
    
}
.rightSideBody{
     display: flex;
     flex-direction: column;
     justify-content: center;
   
   
     
}
.rightSideBody>p{
  padding-top:4rem;
  color: #fff;
  
}
.btnHero{
  padding: 3px 15px;
  margin-top: 15px;
  border-radius: 7px;
  border: none;
  outline: none;
  background-color: solid #fff;
  cursor: pointer;
  font-weight: bold;
}
.btnHero:hover{
  background-color: rgb(219, 219, 224);
}

.body h1{
    font-size: 50px;
}
.card{
    width:100%;
    padding: 0% 13%;
}

.cardTitle{
    padding-right: 10px;
}
.cardInfo{
   width:60%;
   margin-left: auto;


}
.cardInfo>p{
    text-align: left;
}
/* ----beggining services section----*/
.Services{
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 5rem;
    padding: 0% 13%;
    

}
/*services section color pallets*/
.servicesBox{
   display: grid;
   grid-template-columns:repeat(3,1fr);
   gap: 10px;
   margin-top: 2rem;
   width: 100%;
}
.box{
 width: 300px;
 margin: 10px;
 
 display: flex;
 flex-direction: column;
 text-align:center;
 background-color: var(---backGroundColor);
 padding: 2px;
}
.box img{
  width: 300px;
  height:220px;
  margin-bottom: 7px;
  background-position:center;
  background-size:cover;

}
.box h1{
    font-size: 18px;
    margin: 10px 0px;
    font-size:16px ;
    font-weight: 500;
}
.box p{
  font-size: 13px;
  margin-bottom: 5px;
  color:var(---mainTextColor) ;
  
}

/*---end services section----*/


/*-----WHO ARE WE SECTION---*/
.whoCard{
    display: flex;
    flex-direction: column;
   
    height: 15rem;
    margin-top: 4rem;

}


/*-----END WHO ARE WE-----*/
/* ----WHY US SECTION-------*/
.usCard{
    display: flex;
    flex-direction: column;
    text-align: right;
    height:15rem;
    
    margin-top: 4rem;
}
.usInfo{
    width: 50%;
}
.usInfo>p{
    overflow: hidden;
    text-align: left;
}
/*---END WHY US CARD----------*/
/*BEGIN TIPS CARD-------*/
.tipsCard{
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    text-align:center;
    height:auto;
    min-width:100%;
    
   
}
.tipsBody{
  display:grid;
  grid-template-columns: repeat(2,1fr);
  
}
.tip{
  background-color: var(---backGroundColor);
  max-width: 450px;
  height:100px;
  display:flex;
  border-radius: 10px;
  margin: 10px;
  
}
.tip img{
  width: 60px;
  height:60px;
  border-radius: 50%;
  margin:10px;
 
}
.tip p{
  font-size:12px;
  margin-top: 10px;
}

/*------END TIPS----*/
/*-------MEDIA SCREEN----*/
@media only screen and (max-width: 600px) {
  .hero{
    margin-bottom: 2rem;
    width: 100%;
    height:30vh;
    overflow: hidden;
    
  }
  .rightSide{
    margin: 0;
    padding-top: 1.5rem;
    
  }
  .rightSideBody{
    margin: 0;
    padding: 0;
  }
  .rightSideBody>p{
    margin: 0;
    padding: 0;
    font-size: 11px;
  }
  .btnHero{
    padding: 3px 10px;
    font-size:9px;
  }
    .Services{
    display: flex;
    padding: 0;
    margin: 2.5rem 0 0 0 ;
    width: 100%;
    text-align: center; 
    justify-content: center;   
  }
  
.servicesBox{
      display: block;
      padding: 0 15px;
  }
  
  .box{
     width:100%;
     margin:4rem 0;
     padding: 0;
  }
  .card{
    display: flex;
    flex-direction: column;
    padding: 0;
    margin:0;
    text-align: center;
    align-items: center;
  }
.cardTitle{
    padding-right: 10px;
}
.cardInfo{
   margin: 0;
   width: 100%;



}
.cardInfo>p{
    text-align: center;
    width:100%;
   
    margin:0;
    padding: 0;
}

    
  
  .usCard{
    padding: 0;
    margin: 0;
  
    text-align: center;
    margin: 1rem;
    
  }
  .usInfo{
    width: 100%;
  }
  .usCard p{
    text-align: center;
    width: 100%;
   
  }
  .whoCard{
    margin: 1rem ;
    padding: 0;
    text-align: center;
    align-items: left;
    
  }
  .tipsCard{
    height:100%;
  }
  .tipsBody{
    display: flex;
    flex-direction: column;
    margin-top:2rem;
  }
}